.footer { background: #2b2928; border-top: 1px solid #3a3628; padding: 25px 30px; overflow: hidden; }

@media only screen and (max-width: 767px) { .footer { height: auto; } }

.footer__block { position: relative; }

@media only screen and (max-width: 767px) { .footer__block { padding-bottom: 20px; } }

.footer__block__logo { display: flex; align-items: center; }

.footer__block__logo a { width: 39px; height: 40px; display: block; }

.footer__block__logo p { font-size: 12px; letter-spacing: 0.7px; color: #797979; display: flex; padding-left: 10px; }

.footer__block__social a { padding-right: 30px; }

.footer__block__social a:hover svg { fill: #78985c; }

.footer__block__social a svg { font-size: 25px; fill: #d5cba4; transition: all 0.3s; }

.footer__block__social a:last-child { padding-right: 0; }

.footer__block__done { display: flex; align-items: center; justify-content: flex-end; }

.footer__block__done a { font-size: 12px; letter-spacing: 0.7px; text-align: right; color: #797979; display: block; padding-right: 10px; }

.footer__block__done svg { font-size: 30px; fill: #d5cba4; }
